const MyIcon = Icon.createFromIconfontCN({
    scriptUrl:'//at.alicdn.com/t/font_1331187_p4pmcd99vne.js',// 在 iconfont.cn 上生成
});
class LeftMenu extends Component {
    constructor(props){
        super(props);
        this.state = {
            selectedKeys:[],//选中项key
            menuList:[
                // {
                //     url:"/homePage",
                //     name:"主页",
                //     icon:"icon-zhuye",
                // },{
                //     url:"/fillPlan",
                //     name:"填报计划",
                //     icon:"icon-jihua",
                // },{
                //     url:"/myFillingTask",
                //     name:"我的填报任务",
                //     icon:"icon-daibanshixiang",
                // },{
                //     url:"/dataCenter",
                //     name:"教师数据中心",
                //     icon:"icon-shuju1",
                // },{
                //     url:"/auditManagement",
                //     name:"审核管理",
                //     icon:"icon-tianshenpi",
                // },{
                //     url:"/formManagement",
                //     name:"表单管理",
                //     icon:"icon-icon-test",
                // },{
                //     url:"/defectManagement",
                //     name:"纠错管理",
                //     icon:"icon-moshubang",
                // },{
                //     url:"/dataManagement",
                //     name:"数据管理",
                //     icon:"icon-shenjing",
                //     subMenus:[
                //         {
                //             url:"/dataManagement/dataSourceManagement",
                //             name:"数据源管理",
                //             icon:"",
                //         },{
                //             url:"/dataManagement/dataSetManagement",
                //             name:"数据集管理",
                //             icon:"",
                //         }
                //     ]
                // },{
                //     url:"/systemSettings",
                //     name:"系统设置",
                //     icon:"icon-xitongguanli",
                //     subMenus:[
                //         {
                //             url:"/systemSettings/workFlowManagement",
                //             name:"工作流程管理",
                //             icon:"",
                //         },{
                //             url:"/systemSettings/userManagement",
                //             name:"用户管理",
                //             icon:"",
                //         },{
                //             url:"/systemSettings/menuManagement",
                //             name:"菜单管理",
                //             icon:"",
                //         },{
                //             url:"/systemSettings/departmentManagement",
                //             name:"部门管理",
                //             icon:"",
                //         },{
                //             url:"/systemSettings/roleManagement",
                //             name:"角色管理",
                //             icon:"",
                //         }
                //     ]
                // }
            ]
        }
    }
    componentDidMount(){
        try {//避免去locasroage出错，和location出错
            const menuList = JSON.parse(window.localStorage.getItem("leftMenuList")).menus || [];
            this.setState({menuList});
            //避免刷新后获取选择项消失
            let endIndex = this.props.location.pathname.indexOf("?");
            if(endIndex===-1){
                // console.log("没有带参数,可直接设置key值");
                this.setState({
                    selectedKeys:[this.props.location.pathname]
                })
            }else{
                // console.log("带参数，截取前几位为key");
                this.setState({
                    selectedKeys:[this.props.location.pathname.substr(0,endIndex)]
                })
            }
        } catch (error) {
            //可能会取pathname为空报错
            console.log(error);
        } 
    }
    handleClick = ({ item, key }) => {
        // console.log(key);
        // if(key==='/dataCenter'){
        //     getAction('/ybt/auth/getToken').then(res=>{
        //         if(res.success){
        //             let token = res.obj;
        //             window.open(`http://10.175.205.20:8880/#/YBTSSO?token=${token}`)
        //         }else{
        //             message.error(res.obj)
        //         }
        //     })
        // }else{
        //     this.props.history.push(key);
        // }
        this.props.history.push(key);
    }
    //路由变化时更新选择的菜单
    componentDidUpdate(prevProps, prevState){
        try {
            if (prevProps.location.pathname !== this.props.location.pathname){
                let endIndex = this.props.location.pathname.indexOf("?");
                if(endIndex===-1){
                    // console.log("没有带参数,可直接设置key值");
                    this.setState({
                        selectedKeys:[this.props.location.pathname]
                    })
                }else{
                    // console.log("带参数，截取前几位为key");
                    this.setState({
                        selectedKeys:[this.props.location.pathname.substr(0,endIndex)]
                    })
                }
            }
        } catch (error) {
            //可能会取pathname为空报错
            console.log(error);
        }  
    }
    //渲染菜单树
    renderTree = (list) => list.map(item => {
        if(Number(item.type)===1){
            if(item.subMenus && item.subMenus.length>0){
                if(Number(item.subMenus[0].type)===2){
                    return (
                        <Menu.Item key={item.url}>
                            {item.icon?<MyIcon type={item.icon} />:null}
                            <span>{item.name}</span>
                        </Menu.Item>
                    )
                }
                return (
                    <Menu.SubMenu
                        key={item.url}
                        title={
                            <span>
                                {item.icon?<MyIcon type={item.icon} />:null}<span>{item.name}</span>
                            </span>
                        }
                    >
                            {this.renderTree(item.subMenus)}
                    </Menu.SubMenu>
                )
            }else{
                return (
                    <Menu.Item key={item.url}>
                        {item.icon?<MyIcon type={item.icon} />:null}
                        <span>{item.name}</span>
                    </Menu.Item>
                )
            }
        }else{
            return null
        }
    })
    render() {
        return (
            <React.Fragment>
                <div className={`${styles.logo} ${this.props.collapsed?styles.collapsed:""} logo`}>
                    <img src={process.env.PUBLIC_URL+'/logo.png'} alt="logo" style={{cursor:"pointer"}} onClick={() => {this.props.history.push('/homePage')}}/>
                    {
                        this.props.collapsed?null:<span>一表通系统</span>
                    }
                </div>
                <div className={`${styles.menuList} ${this.props.collapsed?styles.collapsed:""}`}>
                    <Menu
                        mode="inline"
                        theme="dark"
                        inlineCollapsed={this.props.collapsed}
                        onClick={this.handleClick}
                        selectedKeys={this.state.selectedKeys}
                    >
                        {this.renderTree(this.state.menuList)}
                    </Menu> 
                </div>
            </React.Fragment>
        )
    }
}

export default withRouter(LeftMenu)