import React, { Component } from 'react'
import { Icon, Popover, Button, Modal, Input, message, Breadcrumb} from 'antd';
import { withRouter,Link} from 'react-router-dom';
import styles from './index.module.less';
import { postAction, getAction } from '@/axios';
const urlListMap = [
    { url:"/fillPlan", text:"填报计划" },
    { url:"/myFillingTask", text:"我的填报任务" },
    { url:"/dataCenter", text:"个人数据中心" },
    { url:"/auditManagement", text:"审核管理" },
    { url:"/formManagement", text:"表单管理" },
    { url:"/defectManagement", text:"纠错管理" },
    { url:"/dataManagement", text:"数据管理" },
    { url:"/systemSettings", text:"系统设置" },
    { url:"/newscenter", text:"消息中心" },
    { url:"/items", text:"个人事项" },
    { url:"/homePage", text:"个人中心" },
]
class TopBread extends Component {
    constructor(props){
        super(props);
        this.state = {
            currentModule:{
                url:"/homePage",
                text:"个人中心"
            },
            status:false, // ‘修改密码弹出框
            oldPassWord: '', // 旧密码
            newPassWord:'', // 新密码
            confirmPassWord: '', // 密码确认
            departName:'', //部门
            name:'', //用户名
            userId:''
        }
    }
    componentDidMount() {
        let userInfo=localStorage.getItem('leftMenuList')
        if(userInfo){
            userInfo=JSON.parse(userInfo)
            this.setState({
                departName:userInfo.departName,
                name:userInfo.name,
                userId:userInfo.userId
            })
        }
        try {
            //避免刷新后顶部导航显示错误消失
            let pathname = this.props.location.pathname;
            let currentModule = urlListMap.find(item => pathname.indexOf(item.url)!==-1);
            if(currentModule){
                this.setState({
                    currentModule:{
                        url:currentModule.url,
                        text:currentModule.text
                    }
                })
            }else{
                this.setState({
                    currentModule:{  url:"", text:"" }
                })
            }
        } catch (error) {
            //可能会取pathname为空报错
            console.log(error);
        } 
    }
    //路由变化时更新currentmodule
    componentDidUpdate(preProps,preState){
        if (preProps.location.pathname !== this.props.location.pathname) {
            let currentModule = urlListMap.find(item => this.props.location.pathname.indexOf(item.url)!==-1);
            if(currentModule){
                this.setState({
                    currentModule:{
                        url:currentModule.url,
                        text:currentModule.text
                    }
                })
            }else{
                this.setState({
                    currentModule:{
                        url:"",
                        text:""
                    }
                })
            }
        } 
    }

    // 弹出密码变更输入框
    changePassWord = () => {
        this.setState({
            status: true
        })
    }

    // 退出登录
    logout = () => {
        getAction('/ybt/auth/logout').then(res=>{
            //正常退出流程
            if(res.success){
                if(window.localStorage.getItem("isSsoAuth")==="true"){
                    //是单点登录进来的,需要调用该接口退出统一认证
                    localStorage.clear();
                    getAction("/ybt/sso/casServerLogOutUrl").then(res=>{
                        if(res.success){
                            message.success("退出成功");
                            window.location.href = res.obj
                        }
                    })
                }else{
                    localStorage.clear();
                    message.success("退出成功");
                    window.location.href = window.location.origin;
                }
            }else{
                message.error("退出失败");
            }
        })
        // localStorage.clear();
        // window.location.href = window.location.origin;
        // window.location.reload(window.location.host);
    }

    // 隐藏修改密码弹出框
    handleCancel = () => {
        this.setState({
            status: false
        })
    }

    // 最终确认修改密码按钮
    handleOk = () => {
        if (!this.state.oldPassWord) {
            message.warn('请输入旧密码！！！',5)
            return false
        } else if (!this.state.newPassWord) {
            message.warn('请输入新密码！！！', 5)
            return false
        } else if (!this.state.confirmPassWord) {
            message.warn('请输入确认密码！！！', 5)
            return false
        }

        if (this.state.newPassWord !== this.state.confirmPassWord) {
            message.error('两次密码输入不一致！！！', 5)
            return false
        }

        postAction('/ybt/system/setting/user/editPassword',{
            userIds:[this.state.userId],
            oldPassword:this.state.oldPassWord,
            password:this.state.newPassWord
        }).then(res=>{
            if(res.success){
                message.success('密码修改成功， 请重新登录！！！')
                setTimeout(()=>{
                    localStorage.clear();
                    window.location.reload(window.location.host);
                },2000)                
            }else{
                message.error(res.obj)
            }
        })
    }

    // 旧密码变更
    oldPassWord = (e) => {
        this.setState({
            oldPassWord: e.target.value
        })
    }

    // 新密码变更
    newPassWord = (e) => {
        this.setState({
            newPassWord: e.target.value
        })
    }

    // 确认密码
    confirmPassWord = (e) => {
        this.setState({
            confirmPassWord: e.target.value
        })
    }
    //切换菜单伸缩
    _toggleMenu = () => {
        this.props.toggleMenu();
    }
    //查看帮助文档
    viewHelp = () => {
        window.open(`http://${window.location.host}/help.pdf`,'_blank')
    }

    render() {
        //单点登录判断，单点登录无修改密码功能
        const isSsoAuth = window.localStorage.getItem("isSsoAuth")==="true"?true:false;
        return (
            <div className={styles.TopBread}>
                <Icon type={this.props.collapsed?"menu-unfold":"menu-fold"}  onClick={this._toggleMenu} className={styles.fold}/>
                <Breadcrumb>
                    <Breadcrumb.Item>
                        <Link to="/homePage">一表通系统</Link>
                    </Breadcrumb.Item>
                    <Breadcrumb.Item>
                        {this.state.currentModule.text}
                    </Breadcrumb.Item>
                </Breadcrumb>

                <div className={styles.userInfo}>
                    {this.state.departName}
                    <span>{this.state.name}</span>
                    <Popover 
                        placement="topLeft"
                        trigger="click"
                        content={
                            (
                                <div style={{width:'120px'}}>
                                    {
                                        isSsoAuth?null:<Button type="link" block onClick={this.changePassWord}>修改密码</Button>
                                    }
                                    <Button type="link" block onClick={this.logout}>退出登录</Button>
                                    <Button type="link" block onClick={this.viewHelp}>帮助文档</Button>
                                </div>
                            )
                        } 
                    >
                        <Icon type="setting" />
                    </Popover>
                </div>

                {
                    <Modal
                        title="修改密码"
                        visible={this.state.status}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                    >
                    <p><span style={{width: "100px", float: "left",lineHeight:"34px"}}>旧密码：</span><Input style={{width: "200px"}} type='password' onChange = {this.oldPassWord} /></p>
                    <p><span style={{width: "100px", float: "left",lineHeight:"34px"}}>新密码：</span><Input style={{width: "200px"}} type='password' onChange = {this.newPassWord} /></p>
                    <p><span style={{width: "100px", float: "left",lineHeight:"34px"}}>密码确认：</span><Input style={{width: "200px"}} type='password' onChange = {this.confirmPassWord} /></p>
                  </Modal>
                }
                
            </div>
        )
    }
}

export default withRouter(TopBread)