import React, {Component, Suspense} from "react";
import {Switch, Route, withRouter, Redirect} from 'react-router-dom';
import LeftMenu from './leftMenu';
import TopBread from './topBread';
import styles from './index.module.less';

//首页
// const HomePage = React.lazy(() => import('./homePage'))
//首页---财大用--根据需求自己切换
const HomePage = React.lazy(() => import('./homePageNew'))

//填报计划
const FillPlan = React.lazy(() => import('./fillPlan'));
//  新建填表计划 Step1
const Step1 = React.lazy(()=> import ('./fillPlan/step1'))
//  新建填表计划 Step2
const Step2 = React.lazy(()=> import ('./fillPlan/step2'))
//  新建填表计划 Step3
const Step3 = React.lazy(()=> import ('./fillPlan/step3'))
//  新建填表计划 Step4
const Step4 = React.lazy(()=> import ('./fillPlan/step4'))
//  新建填表计划 Step5
const Step5 = React.lazy(()=> import ('./fillPlan/step5'))

// 消息中心
const NewsCenter = React.lazy(() => import('./newsCenter'))

//我的填报任务
const MyFillingTask = React.lazy(()=> import ('./myFillingTask'));
//填报任务-step1
const FillingTaskStep1 = React.lazy(() => import('./myFillingTask/step1'));
//填报任务-step2
const FillingTaskStep2 = React.lazy(() => import('./myFillingTask/step2'));
//填报任务-step3
const FillingTaskStep3 = React.lazy(() => import('./myFillingTask/step3'));
//填报任务-step4
// const FillingTaskStep4 = React.lazy(() => import('./myFillingTask/step4'));

//待办/已办事项
const Items = React.lazy(() => import('./items'));

//教师数据中心
const DataCenter = React.lazy(() => import('./dataCenter'));

//汇总记录管理
const SummaryRecordManagement = React.lazy(()=>import('./summaryRecordManagement'))

//审核管理
const AuditManagement = React.lazy(() => import('./auditManagement'));
//审核管理-审核
const Audit = React.lazy(() => import('./auditManagement/audit'));
//审核管理-查看
const AuditLookUp = React.lazy(() => import('./auditManagement/lookUp'));

const AuditPreview = React.lazy(() => import('./auditManagement/auditForm'));
//表单管理
const FormManagement = React.lazy(() => import('./formManagement'));
//表单管理-新建表单-step1
const CreateForm = React.lazy(() => import('./formManagement/createForm'));
//表单管理-选择工作流-step2
const SetWorkFlow = React.lazy(() => import('./formManagement/setWorkFlow'));
//表单管理-创建数据收集表-step3
const DesignForm= React.lazy(() => import('./formManagement/designForm'));
//表单预览
const PreviewForm = React.lazy(() => import('./formManagement/previewForm'));
//表单填报流程预览
const PreviewWorkFlowContainer = React.lazy(() => import('./formManagement/previewWorkFlowContainer'));
//表单管理-上传模板-step4
const UploadTemplate= React.lazy(() => import('./formManagement/uploadTemplate'));
//纠错管理
const DefectManagement = React.lazy(() => import('./defectManagement'));

//数据管理
//数据管理-数据源管理
const DataSourceManagement = React.lazy(() => import('./dataSourceManagement'));
//数据管理-数据集管理
const DataSetManagement = React.lazy(() => import('./dataSetManagement'));

//系统设置
//系统设置-工作流程管理
const WorkFlowManagement = React.lazy(() => import('./workFlowManagement'));
//系统设置-用户管理
const UserManagement = React.lazy(() => import('./userManagement'));
//系统设置-菜单管理
const MenuManagement = React.lazy(() => import('./menuManagement'));
//系统设置-部门管理
const DepartmentManagement = React.lazy(() => import('./departmentManagement'));
//系统设置-角色管理
const RoleManagement = React.lazy(() => import('./roleManagement'));
//系统设置-分组管理
const GroupManagement = React.lazy(() => import('./groupMangement'));
//系统设置-汇总表模板管理
const SummaryTemplateManagement = React.lazy(()=>import('./summaryTemplateManagement'))
//系统设置-汇总表模板管理-新增
const AddSummaryTemplate = React.lazy(()=>import('./summaryTemplateManagement/add'))
//系统设置-汇总表模板管理-编辑
const EditSummaryTemplate = React.lazy(()=>import('./summaryTemplateManagement/edit'))
//流程设计器
const WorkFlowDesign = React.lazy(() => import("./workFlowDesign"))

// 路由
class OtherPage extends Component {
    constructor(props){
        super(props);
        this.state = {
            collapsed:true
        }
    }
    componentDidMount(){
        //关闭浏览器，删除本地存储，防止后续报错
        window.addEventListener('onbeforeunload',()=>{
            window.localStorage.removeItem('leftMenuList');
            window.localStorage.removeItem('loginStatus');
        })
    }
    _toggleMenu = () => {
        this.setState({
            collapsed:!this.state.collapsed
        })
    }
    render() {
        return (
            <div className={styles.otherPageContainer}>
                <div className={`${styles.leftMenu} costom-menu-theme`}>
                    <LeftMenu collapsed={this.state.collapsed}/>
                </div>
                <div className={styles.rightContent}>
                    {/* 标准版顶部导航每页都有---用下面这种方式 */}
                    {/* <TopBread toggleMenu={this._toggleMenu} collapsed={this.state.collapsed}/> */}
                    {/* 财大版本顶部导航homepage页没有----用下面这种方式---根据需求自己切换 */}
                    {
                        this.props.history.location.pathname==="/homePage"?null:<TopBread toggleMenu={this._toggleMenu} collapsed={this.state.collapsed}/>
                    }
                    <div className={styles.content}>
                        <Suspense fallback={<div>Loading...</div>}>
                            <Switch>
                                {/* 首页 */}
                                {/* 标准版---用下面这种方式 */}
                                {/* <Route path="/homePage" exact component={HomePage}/> */}
                                {/* 财大版本---用下面这种方式 */}
                                <Route path="/homePage" exact render={()=>{
                                    return <HomePage toggleMenu={this._toggleMenu} collapsed={this.state.collapsed}/>
                                }}/>
                                {/*填报计划模块路由*/}
                                <Route path="/fillPlan" exact component={FillPlan}/>
                                <Route path="/fillPlan/step1" component={Step1}/>
                                <Route path="/fillPlan/step2" component={Step2}/>
                                <Route path="/fillPlan/step3" component={Step3}/>
                                <Route path="/fillPlan/step4" component={Step4}/>
                                <Route path="/fillPlan/step5" component={Step5}/>
                                {/* 我的填报任务模块路由 */}
                                <Route path="/myFillingTask" exact component={MyFillingTask}/>
                                <Route path="/myFillingTask/step1" component={FillingTaskStep1}/>
                                <Route path="/myFillingTask/step2" component={FillingTaskStep2}/>
                                <Route path="/myFillingTask/step3" component={FillingTaskStep3}/>
                                {/* <Route path="/myFillingTask/step4" component={FillingTaskStep4}/> */}
                                {/*教师数据中心 */}
                                <Route path="/dataCenter" exact component={DataCenter}/>
                                {/*汇总记录管理*/}
                                <Route path="/summaryRecordManagement" exact component={SummaryRecordManagement}/>
                                {/* 审核管理 */}
                                <Route path="/auditManagement" exact component={AuditManagement}/>
                                <Route path="/auditManagement/audit" exact component={Audit}/>
                                <Route path="/auditManagement/lookUp" exact component={AuditLookUp}/>
                                <Route path="/auditManagement/preview" exact component={AuditPreview}/>
                                {/* 表单管理 */}
                                <Route path="/formManagement" exact component={FormManagement}/>
                                <Route path="/formManagement/createForm" exact component={CreateForm}/>
                                <Route path="/formManagement/setWorkFlow" exact component={SetWorkFlow}/>
                                <Route path="/formManagement/designForm" exact component={DesignForm}/>
                                <Route path="/formManagement/previewForm" exact component={PreviewForm}/>
                                <Route path="/formManagement/uploadTemplate" exact component={UploadTemplate}/>
                                <Route path="/formManagement/previewWorkFlowContainer" exact component={PreviewWorkFlowContainer}/>
                                {/* 纠错管理 */}
                                <Route path="/defectManagement" exact component={DefectManagement}/>
                                {/* 数据管理模块路由 */}
                                <Route path="/dataManagement" exact render={(props) => <Redirect to='/dataManagement/dataSourceManagement'/>}/>
                                <Route path="/dataManagement/dataSourceManagement" exact component={DataSourceManagement}/>
                                <Route path="/dataManagement/dataSetManagement" exact component={DataSetManagement}/>
                                {/* 系统设置 */}
                                <Route path="/systemSettings" exact render={(props) => <Redirect to='/systemSettings/workFlowManagement'/>}/>
                                <Route path="/systemSettings/workFlowManagement" exact component={WorkFlowManagement}/>
                                <Route path="/systemSettings/userManagement" exact component={UserManagement}/>
                                <Route path="/systemSettings/menuManagement" exact component={MenuManagement}/>
                                <Route path="/systemSettings/departmentManagement" exact component={DepartmentManagement}/>
                                <Route path="/systemSettings/roleManagement" exact component={RoleManagement}/>
                                <Route path="/systemSettings/groupManagement" exact component={GroupManagement}/>
                                <Route path="/systemSettings/summaryTemplateManagement" exact component={SummaryTemplateManagement}/>
                                <Route path="/systemSettings/summaryTemplateManagement/add" exact component={AddSummaryTemplate}/>
                                <Route path="/systemSettings/summaryTemplateManagement/edit" exact component={EditSummaryTemplate}/>
                                {/*消息中心路由*/}
                                <Route path="/newscenter" component={NewsCenter}/>
                                {/*待办/已办事项*/}
                                <Route path="/items" component={Items} />
                                {/* 流程设计器 */}
                                <Route path="/workFlowDesign" component={WorkFlowDesign} />
                                {/*页面重定向*/}
                                <Route path="/otherpage" render={(props) => <Redirect to='/fillPlan'/>}/>
                                {/*页面重定向*/}
                                <Route path="/" render={(props) => <Redirect to='/homePage'/>}/>
                            </Switch>
                        </Suspense>
                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(OtherPage);