import React from 'react';
import { postAction, getAction } from '@/axios';
import { message } from 'antd';
import loginStyle from './login.module.less'
import Bgimg from './bg.png'

class LoginPage extends React.Component{

    state = {
        username:"",
        password:"",
        isSSo:true,//是否单点登陆
    }

    keyDown = (e) => {
        if(e.keyCode === 13){
            this.loginIn();
        }
    }

    componentDidMount(){
        // 判断是否为统一登陆平台进去的登陆页面
        let n = window.location.href.indexOf("sso=1");
        if (n !== -1) {
            let ticket = window.location.href.split("ticket=")[1];
            localStorage.setItem("ticket", ticket);
            window.localStorage.setItem("isSsoAuth","true");//添加标识是单点登录，用于退出判断
            // 调用接口获取用户信息
            getAction("/ybt/auth/user/info").then(
                (res) => {
                    if(res.success){
                        window.localStorage.setItem("loginStatus","true");
                        window.localStorage.setItem("leftMenuList",JSON.stringify(res.obj))//将左侧菜单数据写入localstrage
                        window.location.reload();
                    }else{
                        message.error(res.obj)
                    }
                }
            )
        }else{ // 正常URL直接进入的登陆页面
            // 判断是否需要走单点登陆，1：是；0：否
            getAction("/ybt/sso/isSsoAuth").then(
                (res) => {
                    if (res.success) {
                        if (res.obj === 1) { // 走单点登录， 去学校统一登录页面
                            window.localStorage.setItem("isSsoAuth","true");//添加标识是单点登录，用于退出判断
                            this.setState({isSSo:true})
                            getAction("/ybt/sso/casServerLoginUrl").then(
                                (res) => {
                                    if (res.success) {
                                        window.location.href = res.obj
                                    }
                                }
                            )
                        } else if (res.obj === 0) { // 走正常登录
                            this.setState({isSSo:false})
                            window.addEventListener("keydown",this.keyDown,false)
                        }
                    }
                }
            )
        }
    }

    componentWillUnmount(){
        window.removeEventListener("keydown",this.keyDown,false)
    }

    loginIn = () => {
        const { username, password } = this.state;
        if(username === ""){
            message.warning("用户名不能为空");
            return false;
        }
        if(password === ""){
            message.warning("密码不能为空");
            return false;
        }
        postAction('/ybt/auth/login',{username,password,rememberMe:"0"},1,1).then(res => {
            if(res.success){
                window.localStorage.setItem("loginStatus","true");
                window.localStorage.setItem("leftMenuList",JSON.stringify(res.obj))//将左侧菜单数据写入localstrage
                window.location.reload();
            }else{
                message.error(res.obj)
            }
        })
    }
	
    render(){
        if(this.state.isSSo){
            return "跳转中..."
        }else{
            return (
                <div className={loginStyle.box}>
                    <div className={loginStyle.top}>
                         <img className={loginStyle.logo} src={process.env.PUBLIC_URL+'/logo.png'} alt=''/>
                         <div className={loginStyle.title}>
                            {window.choolNameConfig}一表通系统
                         </div>
                    </div>
                    <div className={loginStyle.bottom}>
                         <div className={loginStyle.left}>
                             <div className={loginStyle.loginBox}>
                                 <div className={loginStyle.loginTitle}>
                                     请登录您的账户
                                 </div>
                                 <div className={loginStyle.form}>
                                     <p>账号</p>
                                     <input placeholder='请输入账号' autoComplete="new-password" onChange={(e)=>{this.setState({username:e.target.value})}}/>
                                     <p style={{marginTop:20}}>密码</p>
                                     <input placeholder='请输入密码' type="password" autoComplete="new-password" onChange={(e)=>{this.setState({password:e.target.value})}}/>
                                 </div>
                                 <div style={{textAlign:'center'}}>
                                     <div onClick={this.loginIn} className={loginStyle.submit}>登录</div>
                                 </div>
                             </div>
                         </div>
                         <div className={loginStyle.right}>
                                <img src={Bgimg} alt=''/>
                                <p className={loginStyle.copyRight}>版权所有 Copyright &copy; 2019 {window.choolNameConfig}</p>
                         </div>
                    </div>
                    
                </div>
             )
        }
    }
		
}

export default LoginPage;
